function Article(props){
    return(
        <div className="article">
            <h3>{props.title}</h3>
            <h4 className="subtext">{props.subtext}</h4>
            <p>{props.firstP}</p>
            <img src={props.imgLink} alt=""></img>
            <p>{props.secondP}</p>
        </div>

    );


}

export default Article